import {
  Alert,
  Button,
  Grow,
  TextField,
  TextFieldProps,
  Typography,
} from '@mui/material';
import { css, styled } from '@mui/material/styles';
import { DesktopDatePicker } from '@mui/x-date-pickers';
import { userApi } from '@src/apis';
import { RegisterRequestDto } from '@src/apis/dtos';
import { CameraCreate } from '@src/assets/icons';
import theme from '@src/assets/theme/theme';
import { useProfileImageCrop } from '@src/data-binding/model/ProfileImageCrop';
import { Dayjs } from 'dayjs';
import { useRef, useState } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';

export default function SignUpForm() {
  const [birthday, setBirthday] = useState<Dayjs | null>(null);
  const [emailAlert, setEmailAlert] = useState(false);
  const [emailConfirmAlert, setEmailConfirmAlert] = useState(false);
  const [nicknameAlert, setNicknameAlert] = useState(false);
  const [uuid, setUuid] = useState('');
  const [section, setSection] = useState(0);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const profileImageCrop = useProfileImageCrop();

  const {
    register,
    handleSubmit,
    watch,
    trigger,
    getValues,
    formState: { errors },
  } = useForm({ mode: 'onBlur' });

  console.log(errors);

  const onDateChange = async (newValue: Dayjs | null) => {
    setBirthday(newValue);
  };
  const onCertEmail = async () => {
    if (!(await trigger(['email']))) return;
    const { uuid } = await userApi.certEmail(getValues('email'));
    setUuid(uuid);
    setEmailAlert(true);
  };
  const onCertNumber = async () => {
    try {
      if (
        !(await userApi.certNumber(getValues('emailConfirm'), uuid)).success
      ) {
        throw 'success: false';
      }
      setEmailConfirmAlert(true);
    } catch (error) {
      return '  .';
    }
  };
  const onIsNickDouble = async () => {
    try {
      await userApi.isNickDouble(getValues('nickname'));
      setNicknameAlert(true);
    } catch (error) {
      return '  .';
    }
  };
  const onSubmit: SubmitHandler<FieldValues> = async (
    fieldValues: FieldValues,
  ) => {
    try {
      const formData: RegisterRequestDto = {
        birth: fieldValues.birthday.format('YYYY-MM-DD'),
        nickname: fieldValues.nickname,
        pw: fieldValues.password,
        uuid,
        profileImg: profileImageCrop.croppedSrc,
      };
      await userApi.regist(formData);
      location.href = process.env.REACT_APP_PUBLIC_URL!;
      // regist success
    } catch (error) {
      // TODO: 4xx, 5xx
    }
  };
  const checkNextValid = () => {
    return (
      errors.email === undefined &&
      errors.emailConfirm === undefined &&
      errors.password === undefined &&
      errors.passwordConfirm === undefined &&
      getValues().email &&
      getValues().emailConfirm &&
      getValues().password &&
      getValues().passwordConfirm
    );
  };

  const formCss = css`
    display: flex;
    justify-content: left;
    align-items: center;
    margin-top: 60px;
    width: 100%;
    flex-wrap: nowrap;
    position: relative;
    transform: ${section === 1 ? 'translateX(calc(-100% - 144px))' : '0'};
    transition-duration: 0.5s;
  `;
  const nextButtonCss = css`
    display: block;
    margin-top: 10px;
    width: 100%;
    height: 40px;
    color: white;
    background-color: ${checkNextValid()
      ? theme.palette.secondary.main
      : theme.palette.gray2.main};
    border-radius: 10px;

    &:hover {
      color: ${checkNextValid() ? theme.palette.primary.main : 'none'};
      background-color: ${checkNextValid() ? 'none' : theme.palette.gray2.main};
      border: ${checkNextValid()
        ? `1px solid ${theme.palette.primary.main}`
        : 'none'};
    }
  `;

  return (
    <Root>
      <form css={formCss} onSubmit={handleSubmit(onSubmit)}>
        <SignUpFirstFormWrapper>
          <SignUpFormControl>
            <SignUpFormLabel variant="h4"> </SignUpFormLabel>
            <SingUpFormTextField
              {...register('email', {
                required: ' .',
                pattern: {
                  value: /\S+@\S+\.\S+/,
                  message: '  .',
                },
              })}
              type="email"
              error={!!errors.email}
              placeholder=" ."
              helperText={errors.email?.message as string}
              variant="outlined"
              size="small"
            />
            <SignUpFormButton onClick={onCertEmail}></SignUpFormButton>

            <Grow in={emailAlert}>
              <SignUpAlert
                severity="info"
                sx={{ display: emailAlert ? 'flex' : 'none' }}
              >
                  .  .
              </SignUpAlert>
            </Grow>
          </SignUpFormControl>

          <SignUpFormControl>
            <SignUpFormLabel variant="h4"> </SignUpFormLabel>
            <SingUpFormTextField
              {...register('emailConfirm', {
                required: '  .',
                valueAsNumber: true,
                validate: onCertNumber,
              })}
              type="number"
              helperText={errors.emailConfirm?.message as string}
              error={!!errors.emailConfirm}
              placeholder="  ."
              variant="outlined"
              size="small"
            />
            <SignUpFormButton
              onClick={() => {
                trigger(['emailConfirm']);
              }}
            >
              
            </SignUpFormButton>
            <Grow in={emailConfirmAlert}>
              <SignUpAlert
                severity="success"
                sx={{ display: emailConfirmAlert ? 'flex' : 'none' }}
              >
                 .
              </SignUpAlert>
            </Grow>
          </SignUpFormControl>

          <SignUpFormControl>
            <SignUpFormLabel variant="h4"></SignUpFormLabel>
            <SingUpFormTextField
              {...register('password', {
                pattern: {
                  value: /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/,
                  message: ',   8  .',
                },
              })}
              type="password"
              helperText={errors.password?.message as string}
              error={!!errors.password}
              placeholder=",   8 "
              variant="outlined"
              size="small"
            />
          </SignUpFormControl>

          <SignUpFormControl>
            <SignUpFormLabel variant="h4"> </SignUpFormLabel>
            <SingUpFormTextField
              {...register('passwordConfirm', {
                validate: (val: string) => {
                  if (watch('password') !== val) {
                    return '  .';
                  }
                },
              })}
              type="password"
              helperText={errors.passwordConfirm?.message as string}
              error={!!errors.passwordConfirm}
              placeholder="  ."
              variant="outlined"
              size="small"
            />
          </SignUpFormControl>

          <Button
            css={nextButtonCss}
            onClick={() => {
              if (checkNextValid()) {
                setSection(1);
              }
            }}
          >
            
          </Button>
        </SignUpFirstFormWrapper>

        <SignUpSecondFormWrapper>
          <SignUpFormControl>
            <SignUpFormLabel variant="h4"> </SignUpFormLabel>
            <ProfileImageButtonWrapper>
              <ProfileImageButton
                onClick={() => {
                  fileInputRef.current!.click();
                }}
              >
                {profileImageCrop.croppedSrc !== '' ? (
                  <ProfileImage src={profileImageCrop.croppedSrc} />
                ) : (
                  <EmptyProfileImage src="https://fanfixiv.s3.ap-northeast-2.amazonaws.com/profile/origin/profile-img.png" />
                )}
                <CameraCreateIcon viewBox="0 0 48 48" />
              </ProfileImageButton>
              <input
                type="file"
                ref={fileInputRef}
                hidden
                onChange={(e) => {
                  if (e.currentTarget.files === null) return;
                  const reader = new FileReader();
                  reader.addEventListener('load', () => {
                    profileImageCrop.startCrop(reader.result as string);
                    fileInputRef.current!.value = '';
                  });
                  reader.readAsDataURL(e.currentTarget.files[0]);
                }}
              />
            </ProfileImageButtonWrapper>
          </SignUpFormControl>
          <SignUpFormControl>
            <SignUpFormLabel variant="h4"></SignUpFormLabel>
            <SingUpFormTextField
              {...register('nickname', {
                required: ' .',
                validate: onIsNickDouble,
              })}
              variant="outlined"
              size="small"
              helperText={errors.nickname?.message as string}
              error={!!errors.nickname}
            />
            <SignUpFormButton onClick={onIsNickDouble}>
              
            </SignUpFormButton>

            <Grow in={nicknameAlert}>
              <SignUpAlert
                severity="success"
                sx={{ display: nicknameAlert ? 'flex' : 'none' }}
              >
                 .
              </SignUpAlert>
            </Grow>
          </SignUpFormControl>

          <SignUpFormControl>
            <SignUpFormLabel variant="h4"></SignUpFormLabel>
            <DesktopDatePicker
              inputFormat="YYYY.MM.DD"
              value={birthday}
              openTo="year"
              onChange={onDateChange}
              renderInput={(props: TextFieldProps) => (
                <SingUpFormTextField
                  {...props}
                  {...register('birthday', {
                    required: ' .',
                    setValueAs: () => birthday,
                  })}
                  helperText={errors.birthday?.message as string}
                  error={!!errors.birthday}
                  variant="outlined"
                  size="small"
                />
              )}
            />
          </SignUpFormControl>

          <SignUpButton type="submit"></SignUpButton>
        </SignUpSecondFormWrapper>
      </form>
    </Root>
  );
}

const Root = styled('div')`
  width: 100%;
  height: 100%;
  position: relative;
`;

const SignUpFirstFormWrapper = styled('div')`
  width: 100%;
  flex-shrink: 0;
`;

const SignUpSecondFormWrapper = styled('div')`
  width: 100%;
  flex-shrink: 0;
  margin-left: 144px;
`;

const SignUpFormControl = styled('div')`
  display: flex;
  flex-wrap: wrap;
  min-height: 87px;
  margin-bottom: 16px;
  gap: 4px;
  margin-top: 8px;
`;

const SignUpFormLabel = styled(Typography)`
  width: 100%;
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 10px;
  font-weight: bold;
`;

const SingUpFormTextField = styled(TextField)(
  ({ theme }) => `
    flex-grow: 1;

    & .MuiInputBase-root {
      border-radius: 10px;
    }
    & .MuiOutlinedInput-notchedOutline {
      border: 1px solid ${theme.palette.secondary.main};
    }
  `,
);

const ProfileImageButtonWrapper = styled('div')`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ProfileImageButton = styled('div')`
  border-radius: 50%;
  width: 160px;
  height: 160px;
  position: relative;
  cursor: pointer;
`;

const EmptyProfileImage = styled('img')(
  ({ theme }) => `
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: ${theme.palette.gray2.main};
    position: absolute;
    left: 0;
    top: 0;
  `,
);

const ProfileImage = styled('img')(
  ({ theme }) => `
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: ${theme.palette.gray2.main};
    position: absolute;
    left: 0;
    top: 0;
  `,
);

const CameraCreateIcon = styled(CameraCreate)`
  position: absolute;
  right: 0;
  bottom: 0;
  width: 48px;
  height: 48px;
`;

const SignUpFormButton = styled(Button)(
  ({ theme }) => `
    width: 100px;
    height: 40px;
    color: white;
    background-color: ${theme.palette.secondary.main};
    border-radius: 10px;
    
    &:hover {
      color: ${theme.palette.primary.main};
      border: 1px solid ${theme.palette.primary.main};
    }
  `,
);

const SignUpButton = styled(Button)(
  ({ theme }) => `
    display: block;
    margin-top: 10px;
    width: 100%;
    height: 40px;
    color: white;
    background-color: ${theme.palette.secondary.main};
    border-radius: 10px;
    
    &:hover {
      color: ${theme.palette.primary.main};
      border: 1px solid ${theme.palette.primary.main};
    }
  `,
);

const SignUpAlert = styled(Alert)`
  width: '100%';
  flex-grow: 1;
  margin-top: 2px;
  border-radius: 10px;
`;
